*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body
{
    background-color: black;
}
.nav-c
{
    display: flex;
    justify-content: space-around;
    background-color: #292828;
    padding-top: 2vh;
    padding-bottom: 2vh;
}
#kb-Icon img
{
    height: 4vh;
}
#kb-T img
{
    height: 3.5vh;
}
.Search-bar input
{
    width: 32vw;
    height: 5vh;
    background-color: #373737;
    border: 1px solid #373737;
}
.Search-bar button
{
    height: 5vh;
    width: 8vw;
    margin-left: 1vw;
    background-color: #292828;
    color: white;
    border: 1px solid white;
}
input[type='text']
{
    color: white;
}
.Options a
{
    margin: 1vw;
}
.Options a img
{
    height: 5vh;
}